.homeScreen .appContent{
    margin-top: 0 !important;
    background-color: #f1f7f5;
}

.homeScreen .homeBanners{
    height: 186px !important;
}

.homeScreen .homeBannerItem{
    width: 100%;
    height: 186px !important;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

.homeScreen .topMenuRow{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    background-color: white;
    border-bottom: 1px solid #f0f0f0;
}

.homeScreen .topMenuItem{
    -ms-flex: 1 1;
        flex: 1 1;
    height: 80px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    font-size: 14px;
    font-weight: 300;
    color: #333333;
}

.homeScreen .topMenuIcon{
    width: 40px;
    height: 40px;
    margin-bottom: 5px;
}

.homeScreen .centerMenuRows{
    border-top: 1px solid #f0f0f0;
    margin-top: 8px;
}

.homeScreen .centerMenuIcon{
    width: 40px;
    height: 30px;
    margin-bottom: 5px;
}

.homeScreen .centerMenuItem{
}

.homeScreen .centerMenuItemBorder{
    border-right: 1px solid #f0f0f0;
}


