/**酒店**/
.hotelDetail .navBar{
    background-color: rgba(255, 255, 255, 0) !important;
}
.hotelDetail .appContent{
    margin-top: 0px !important;
    margin-bottom: 50px;
}
.hotelRecommendListBottomText{
    color: white;
    font-size: 17px;
    font-weight: 300;
    margin-left: 15px;
    margin-bottom: 13px;
    margin-right: 10px;
    -ms-flex: 1 1;
        flex: 1 1;
    white-space: nowrap;
    -o-text-overflow:ellipsis;
       text-overflow:ellipsis;
    overflow: hidden;
}

.hotelRecommendListBottomStars{
    margin-right: 15px;
    margin-bottom: 10px;
}

.hotelDetailFacilities{
    display: -ms-flexbox;
    display: flex;
    margin: 15px 0px 15px 0px;
}
.hotelDetailFacility{
    -ms-flex:1 1;
        flex:1 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
}
.hotelDetailFacilityIcon{
    width: 40px;
    height: 35px;
}
.hotelDetailFacilityFont{
    font-size: 12px;
    margin-top: 10px;
    color:#333333;
}
.hotelDetailTitle{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    padding: 10px 15px 10px 15px;
    border-bottom: 1px solid #f0f0f0;    
}
.hotelDetailTitleRight{
    border-left: 3px solid #6dd3ad;
    padding-left: 10px;
    color: #333333;
}
.hotelDetailTitleCenter{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}
.hotelRoomInDate{
    color: #333333;
    font-size: 13px;
}
.hotelRoomInDateFont{
    font-size: 13px;
    color: #999999;
    margin-left: 5px;
}
.hotelRoomOutDate{
    margin-left: 10px;
    color: #333333;
    font-size: 13px;    
}
.hotelRoomOutDateFont{
    font-size: 13px;
    color: #999999;
    margin-left: 5px;    
}
.hotelDetailTitleLeft{
    font-size: 13px;
    color: #999999;
}
.hotelRoomItemImg{
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    width: 80px;
    height: 80px;
    border-radius: 5px;
}
.hotelRoomItem{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    padding: 10px;
    border-bottom: 1px solid #f0f0f0;
}
.hotelRoomItemLeft{
    display: -ms-flexbox;
    display: flex;
}
.hotelRoomContent{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;    
}   
.hotelRoomItemRight{
    margin-left: 15px;
    margin-right: 5px;
    padding-top: 12px;
    padding-bottom: 12px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}
.hotelRoomItemPrice{
    color: #e76b53;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: baseline;
        align-items: baseline;
}
.hotelRoomItemPriceValue{
    margin-left: 3px;
    font-size: 20px;
}
.hotelRoomItemPriceLabel{
    font-size: 12px;
    color: #999999;
}
.hotelRoomItemButton{
    padding: 7px 20px 7px 20px;
    background-color: #6dd3ad;
    color: white;
    border-radius: 3px;
    font-size: 13px;
    font-weight: 300;
}
.hotelRoomTip{
    display: -ms-flexbox;
    display: flex;
    margin-top: 6px;    
}
.hotelRoomTips{
    border: 1px solid #999999;
    color: #999999;
    border-radius: 3px;
    padding: 2px 4px 2px 4px;
    font-size: 12px;
    margin-left: 8px;
}
.hotelRoomName{
    color:#333333;
    font-size: 18px;
    margin-left: 8px;    
}
.hotelRoomConfig{
    color:#666666;
    font-size: 15px;
    margin-left: 8px;    
    margin-top: 6px;
}


