body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}

/**全局**/
.navBar{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 100;
    background-color: #6dd3ad !important;
}

.appContent{
    position: relative;
    overflow: auto;
    margin-top: 45px;
    background-color: white;
    -webkit-overflow-scrolling: touch;
}

.loadingMore{
    padding: 20px 0 40px 0;
}

.appContent::-webkit-scrollbar {display:none}

.navBar .am-segment-item{
    padding: 0 8px !important;
}

.navBar .am-segment-item-selected{
    color: #333333 !important;
}

.tabBarIcon{
    width: 40px;
    height: 30px;
}

.am-tabs-default-bar-underline{
    border-color: #6dd3ad !important;
}

.am-tabs-default-bar-tab-active{
    color: #6dd3ad !important;
}

.listBox{
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
}

.emptyView{
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

.emptyViewImage{
    width: 201px;
    height: 165px;
}



