.mapLocationPage{
    position: relative;
}

.mapLocationPage .navBar{
    position: absolute !important;
}

.mapLocationPage .BMap_noprint{
    bottom: 110px !important;
}

.mapMenuBox{
    position: absolute;
    bottom: 40px;
    height: 60px;
    display: -ms-flexbox;
    display: flex;
    background: white;
    width: calc(100% - 20px);
    margin: 0 10px;
    font-size: 0.8em;
    border-radius: 3px;
}

.mapMenuItem{
    -ms-flex: 1 1;
        flex: 1 1;
    -ms-flex-pack: center;
        justify-content: center;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center; 
    border-right: 1px solid #999;
    margin: 10px 0;
}

.mapMenuItem:last-child{
    border-right-width: 0;
}

.mapMenuItemIcon{
    width: 18px;
    height: 18px;
}

.mapMenuItemTitle{
    margin-top: 5px;
}

.mapSearchBox{
    position: absolute;
    top: 60px;
    width: calc(100% - 20px);
    margin: 0 10px;
    border-radius: 3px;
    padding: 8px;
    display: -ms-flexbox;
    display: flex;
    background-color: white;
    -ms-flex-align: center;
        align-items: center;
}

.mapSearchBar{
    -ms-flex: 1 1;
        flex: 1 1;
    background-color: transparent;
    height: 30px;
    border-right: 1px solid #ccc;
}

.mapSearchBar .am-search-cancel{
    color: #999;
}

.mapSearchMenu{
    height: 25px;
    margin: 0 10px;
}

.mapRightButton{
    margin-right: 10px;
}

.mapRightButtonImage{
    width: 25px;
    height: 25px;
}

.mapSearchListItemBg{
    background-color: #efeff4;
    padding: 10px 15px 0;
}

.mapSearchListItem{
    background: white;
}

.mapSearchListItemHead{
    display: -ms-flexbox;
    display: flex;
    padding: 10px;
}

.mapSearchListItemHeadInfo{
    -ms-flex: 1 1;
        flex: 1 1;
    margin-left: 10px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction:column;
        flex-direction:column;
    line-height: 10vw;
}

.mapSearchListItemHeadImg{
    width: 25vw;
    height: 20vw;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

.mapSearchListItemHeadName{
    -ms-flex: 1 1;
        flex: 1 1;
    -ms-flex-align: center;
        align-items: center;
}

.mapSearchListItemHeadAddress{
    font-size: 0.9em;
    color: #999;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    width: 57vw;
    -ms-flex: 1 1;
        flex: 1 1;
    -ms-flex-align: center;
        align-items: center;
}

.mapSearchListItemBottom{
    border-top: 1px solid #ccc;
    padding: 10px 0;
    display: -ms-flexbox;
    display: flex;
    font-size: 0.9em;
}

.mapSearchListItemBottom .item{
    -ms-flex: 1 1;
        flex: 1 1;
    border-right: 1px solid #ccc;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

.mapSearchListItemBottom .item img{
    width: 15px;
    height: 15px;
    margin-right: 5px;
}

.mapSearchListItemBottom .item:last-child{
    border-right-width: 0;
}


