.activityListItemImg{
    width: 100%;
    height: 170px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}
.activityListItemTextBox{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    padding: 10px;
}
.activityListItemRight{
    background-color: #6dd3ad;
    padding: 5px;
    color:#ffffff;
    border-radius: 5px;
}
.activityListItemTitle{
    color:#000000;
    font-size: 15px;
    font-weight: 500;
}
.activityListItemTime{
    color:#666666;
    font-size: 13px;
    font-weight: 400;
    margin-top: 10px;
}
.activityDetailContentView{
    margin-bottom: 60px;
}
.activityDetailHeaderImg{
    width: 100%;
    height: 200px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}
.activityDetailContent{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}
.activityDetailTitle{
    -ms-flex-item-align: center;
        align-self: center;
    margin-top: 15px;
}
.activityDetailDays{
    color: #fd8f37;
    -ms-flex-item-align: center;
        align-self: center;
    font-size: 13px;
    padding: 5px;
    border: #fd8f37 solid 1px;
    border-radius: 5px;
    margin-top: 15px;
    margin-bottom: 5px;
}
.activity .navBar{
    background-color: rgba(255, 255, 255, 0) !important;
}

.activity .appContent{
    margin-top: 0px !important;
}

.activityDetailTimeDiv{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    margin-left: 20px;
    margin-top: 10px;
}
.activityDetailTimeIcon{
    width: 21px;
    height: 25px;
}
.activityDetailTime{
    margin-left: 10px;
    color: #333333;
    font-size: 15px;
}
.activityDetailDescribes{
    padding: 20px;
    line-height: 130%;    
}
.activityDetailSplitLine{
    height: 5px;
    border-bottom: 1px solid #dddddd;
    margin-top: 5px; 
    color: #333333;
    font-size: 15px;
}
.activityDetailSign{
    position: fixed;
    bottom: 0px;
    padding-top: 20px;
    padding-bottom: 20px;
    text-align: center;
    color: #ffffff;
    background-color: #fd8f37;
    width: 100%;
}


